import uniIconfont from '@/components/uni-iconfont/uni-icon.vue'
import bugBtn from '@/components/uni-bug-btn/uni-bug-btn.vue';
import {turnPage} from '@/common/global.js';
export default {
	name: 'GoodList',
	components: {
		uniIconfont,
		bugBtn
	},
	data () {
		return {
			select: false
		}
	},
	props: {
		// 是否需要勾选框
		nselect: {
			type: Boolean,
			default: false
		},
		// 最小化显示
		small: {
			type: Boolean,
			default: false
		},
		value: {
			type: Array,
			default: function () {
				return [];
			}
		}
	},
	computed: {},
	watch: {
		value () {
			this.initValueData();
		}
	},
	onLoad() {
		this.initValueData();
	},
	methods: {
		// 处理传入的数据
		initValueData () {
			let _this = this;
			for (let i in _this.value) {
				let item = _this.value[i];
				if (!item.count && item.count !== 0) {
					item.count = 0;
				}
			}
			console.log(_this.value);
		},
		// 选中商品
		clkChoose (e, data) {
			if (e.error) {
				uni.showToast({title: '无法购买更多', icon: 'none', position: 'bottom'});
			} else {
				this.$store.commit('addGoodToCar', [data, e.num]);
			}
		},
		clkLine (data) {
			turnPage('detail', data);
		}
	}
}
